.header {
	display: flex;
	align-items: center;
	position: sticky;
	top: 0;
	height: 70px;
	background-color: #f6f6f6;
	color: rgba(63, 66, 87);
	z-index: 100;
}

.header__container {
	flex: 1;
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: 70%;
	margin: 0 auto;
}

.header__title {
	margin-left: 20px;
}

.header__title > h4 {
	text-transform: uppercase;
	font-weight: 500;
}

.header__title > h6 {
	text-transform: uppercase;
	font-weight: 400;
}

.header__logo {
	display: flex;
	justify-content: center;
	align-items: center;
}

.header__logo h4 > {
	display: flex;
}

.header__logo > img {
	object-fit: contain;
	width: 50px;
}
